.inputBox {
    display: flex;
    align-items: center;
    justify-content: center;
    /*font-size: 1.3rem;*/
    margin: 4px 0;
    position: relative;
}

.inputBox:first-of-type {
    margin-top: 3rem;
}

.label {
    font-size: 1rem;
    /*color: #b1b1b1;*/
    position: absolute;
    /*padding: 8px;*/
    margin-left: 11px;
    margin-top: -26px;
}

.name {

}

.job {

}

.input {
    font: inherit;
    font-size: 1rem;
    display: inline-block;
    border: 3px solid transparent;
    background: #eeeeee;
    padding: 8px;
    color: black;
    width: 50%;
    transition: all .5s;
    margin-bottom: 2rem;
}

.input::placeholder {
    color: #b1b1b1;
}

.input:focus {
    outline: none;
    border: 3px solid #4141ff;
}

.inputBox .input:active {
    color: red;
}


.button {
    padding: 1rem 2rem;
    color: white;
    background: #4141ff;
    transition: background-color .2s, transform .2s;
    font-size: 1rem;
    border: none;
    outline: none;
    margin: 1rem;
}

.button:hover {
    background: #2525cd;
    outline: none;
    transform: translateY(-3px);
}

.button:active {
    transform: translateY(-1px);
}